<template>
  <div>
    <div v-if="personLoaded">
      <div v-if="!mobile" class="app flex flex-column">
        <Navigation />
        <div class="app-content flex flex-content">
          <Modal v-if="modalActive" />
          <ModalNotification v-if="modalNotif" />
          <ModalNote v-if="modalNote" />
          <ModalSearch v-if="searchModal" />
          <ModalDelete v-if="modalDelete" />
          <transition name="person">
            <PersonModal v-if="personModal" />
          </transition>
          <router-view />
        </div>
      </div>
      <div v-else class="mobile-message flex flex-column">
        <h2>Sorry ! This app is not supported on Mobile Device</h2>
        <p>To use this app, please use a Computer or Tablet</p>
      </div>
    </div>
    <div v-else class="offline flex">
      Connexion à internet...! Merci de patienter
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import Navigation from "./components/Navigation.vue";
import PersonModal from "./components/PersonModal.vue";
import Modal from "./components/Modal.vue";
import ModalNotification from "./components/ModalNotification.vue";
import ModalNote from "./components/ModalNote.vue";
import ModalSearch from "./components/ModalSearch.vue";
import ModalDelete from "./components/ModalDelete.vue";
export default {
  name: "App",
  components: {
    Navigation,
    PersonModal,
    Modal,
    ModalNotification,
    ModalNote,
    ModalSearch,
    ModalDelete,
  },
  data() {
    return {
      mobile: null,
    };
  },
  created() {
    this.GET_ALL_PERSONS();
    this.checkScreen();
    window.addEventListener("resize", this.checkScreen);
  },
  mounted() {
    this.checClavier();
  },
  methods: {
    ...mapActions(["GET_ALL_PERSONS"]),
    ...mapMutations(["TOGGLE_SEARCH_MODAL", "TOGGLE_PERSON"]),
    checkScreen() {
      const windowWidth = window.innerWidth;
      if (windowWidth <= 750) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
    },
    toggleModalSearch() {
      this.TOGGLE_SEARCH_MODAL();
    },

    togglePersonModal() {
      this.TOGGLE_PERSON();
    },
    checClavier() {
      const clavier_espace = 32;
      const clavier_escap = 27;
      window.onkeydown = (e) => {
        if (e.ctrlKey && e.which == clavier_espace) {
          if (!this.searchModal) {
            this.toggleModalSearch();
          }
          if (this.personModal) {
            this.togglePersonModal();
          }
        }
        if (e.keyCode === clavier_escap) {
          if (this.personModal) {
            this.togglePersonModal();
          }
          if (this.searchModal) {
            this.toggleModalSearch();
          }
        }
      };
    },
  },
  computed: {
    ...mapState([
      "personModal",
      "modalActive",
      "personLoaded",
      "modalNotif",
      "searchModal",
      "modalNote",
      "modalDelete",
    ]),
  },
};
</script>

