<template>
  <router-link
    class="person flex"
    :class="persons.status"
    :to="{ name: 'Details', params: { idPerson: persons.id } }"
  >
    <div class="left flex">
      <span class="id">{{ persons.numero }}</span>
      <span>{{ persons.nom }}</span>
      <span>{{ persons.prenom }}</span>
    </div>
    <div class="right flex">
      <span class="date">{{ formatDate(persons.date_d_inscription) }}</span>
      <div class="status-button flex">
        <span :class="persons.validation == 'OUI' ? 'valide' : 'non-valide'">{{
          persons.validation
        }}</span>
      </div>
      <div class="icon">
        <img src="@/assets/icon-arrow-right.svg" alt="" />
      </div>
    </div>
  </router-link>
</template>

<script>
export default {
  name: "listPerson",
  props: ["persons"],
  data() {
    return {
      dateOption: { year: "numeric", month: "short", day: "numeric" },
    };
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleDateString("fr-FR", this.dateOption);
    },
  },
};
</script>

<style lang="scss" scoped>
.person {
  &.OK {
    border-left: 5px solid green;
  }
  &.WAITING {
    border-left: 5px solid yellow;
  }
  &.KO {
    border-left: 5px solid red;
  }
  text-decoration: none;
  cursor: pointer;
  gap: 16px;
  margin-bottom: 16px;
  color: #f1f1f1;
  border-radius: 16px;
  padding: 28px 32px;
  background-color: #1e2329;
  align-items: center;
  span {
    font-size: 14px;
    background-color: #1e2329;
  }
  .left {
    width: 60%;
    flex-basis: 60%;
    gap: 16px;
    background-color: #1e2329;

    span {
      flex: 1;
      &.id {
        text-transform: uppercase;
      }
    }
  }

  .right {
    background-color: #1e2329;
    gap: 16px;
    flex-basis: 40%;
    align-items: center;
    .icon,
    img {
      background-color: #1e2329;
    }
    .date {
      flex: 1;
      font-size: 14px;
      font-weight: 600;
    }
    .status-button {
      background: #1e2329;
      width: 30px;
      height: 30px;
      justify-content: center;
      align-items: center;
      span {
        padding: 5px 10px;
        border-radius: 5px;
        &.valide {
          background: #09e051;
        }
        &.non-valide {
          background: #ff9100;
        }
      }
    }
  }
}
</style>
