<template>
  <div class="modal flex">
    <div class="modal-content">
      <p>Are you sure you want to exit? Your changes will not saved?</p>
      <div class="actions flex">
        <button @click="closeModal" class="purple">Return</button>
        <button @click="closePerson" class="red">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations,mapState } from 'vuex';
export default {
  name: "modal",
  methods:{
    ...mapMutations(['TOGGLE_MODAL','TOGGLE_PERSON','TOGGLE_UPDATE_PERSON']),
    closeModal(){
      this.TOGGLE_MODAL();
    },
    closePerson(){
      this.TOGGLE_MODAL();
      this.TOGGLE_PERSON();
      if(this.editPerson){
        this.TOGGLE_UPDATE_PERSON();
      }
    },
  },computed:{
    ...mapState(['editPerson'])
  }
};
</script>

<style lang="scss" scoped>
.modal {
  z-index: 100;
  position: fixed;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background: transparent;
  .modal-content {
    border-radius: 20px;
    padding: 48px 32px;
    max-width: 450px;
    background-color: #252945;
    color: #fff;
    p {
      text-align: center;
      background: transparent;
    }
    .actions {
      margin-top: 24px;
      background: transparent;

      button {
        flex: 1;
      }
    }
  }
}
</style>
