import { createStore } from "vuex";
import axios from "axios";

const URL =
  "https://script.google.com/macros/s/AKfycbwAhbQyJt5HYf-Auvs3w5QmxrWZisgmm4_BrZmtmS51pz2KqdAXjDEviFw41ChhKCI/exec";
export default createStore({
  state: {
    personModal: null,
    modalActive: null,
    personData: [],
    personLoaded: false,
    currentPersonArray: null,
    editPerson: null,
    validatedPerson: null,
    modalNotif: null,
    modalDelete: null,
    modalNote: null,
    searchModal: null,
    searchValue: null,
  },
  mutations: {
    TOGGLE_PERSON(state) {
      state.personModal = !state.personModal;
    },
    TOGGLE_MODAL(state) {
      state.modalActive = !state.modalActive;
    },
    TOGGLE_NOTIF_MODAL(state) {
      state.modalNotif = !state.modalNotif;
    },
    TOGGLE_NOTE_MODAL(state) {
      state.modalNote = !state.modalNote;
    },
    TOGGLE_DELETE_MODAL(state) {
      state.modalDelete = !state.modalDelete;
    },
    TOGGLE_SEARCH_MODAL(state) {
      state.searchModal = !state.searchModal;
    },
    SET_PERSON_DATA(state, payload) {
      state.personData.push(payload);
    },

    SET_SEARCH_VALUE(state, payload) {
      state.searchValue = payload;
    },

    PERSON_LOADED(state) {
      state.personLoaded = true;
    },
    SET_CURRENT_PERSON(state, payload) {
      state.currentPersonArray = state.personData.filter((person) => {
        return person.id == payload;
      });
    },
    TOGGLE_UPDATE_PERSON(state) {
      state.editPerson = !state.editPerson;
    },
    TOGGLE_VALIDATE_PERSON(state) {
      state.validatedPerson = true;
    },
    DELETE_PERSON(state, payload) {
      state.personData = state.personData.filter((pers) => pers.id !== payload);
    },
  },
  actions: {
    async GET_ALL_PERSONS({ commit, state }) {
      const result = await axios
        .get(`${process.env.VUE_APP_BASE_URL}?action=read`)
        .then((res) => {
          return res.data;
        });

      result.forEach((doc) => {
        // raha efa miexiste amle data ilay checkena am base d ts pushena
        if (!state.personData.some((pers) => pers.id === doc.id)) {
          const data = {
            id: doc.id,
            nom: doc.nom,
            prenom: doc.prenom,
            numero: doc.numero,
            email: doc.email,
            univ: doc.univ,
            telephone: doc.telephone,
            CIN: doc.CIN,
            sexe: doc.sexe,
            date_d_inscription: doc.date_d_inscription,
            validation: doc.validation,
            status: doc.status,
            description: doc.description,
          };

          commit("SET_PERSON_DATA", data);
        }
      });

      commit("PERSON_LOADED");
    },

    async UPDATE_PERSON({ commit, dispatch }, { id, routeId }) {
      commit("DELETE_PERSON", id);
      await dispatch("GET_ALL_PERSONS");
      commit("TOGGLE_PERSON");
      commit("TOGGLE_UPDATE_PERSON");
      commit("SET_CURRENT_PERSON", routeId);
    },
    async VALIDATE_PERSON({ commit, dispatch, state }, { id, routeId }) {
      commit("DELETE_PERSON", id);
      await dispatch("GET_ALL_PERSONS");
      commit("TOGGLE_VALIDATE_PERSON");
      commit("SET_CURRENT_PERSON", routeId);
    },
    async NOTE_PERSON({ commit, dispatch, state }, { id, routeId }) {
      commit("DELETE_PERSON", id);
      await dispatch("GET_ALL_PERSONS");
      commit("TOGGLE_NOTE_MODAL");
      commit("SET_CURRENT_PERSON", routeId);
    },

    async DELETE_THE_PERSON({ commit, state }, id) {
      await axios
        .post(`${process.env.VUE_APP_BASE_URL}?action=delete&id=${id}`)
        .then((res) => {
          commit("DELETE_PERSON", id); //delete person on the state
          state.modalDelete = false;
        })
        .catch((err) => console.log(err));
    },

    async SEARCH_PERSON({ commit }, value) {
      commit("SET_SEARCH_VALUE", value);
    },
  },
  modules: {},
});
