<template>
  <div
    class="person-wrap flex flex-column"
    @click="checkClick"
    ref="personWrap"
  >
    <form @submit.prevent="submitForm" class="person-content">
      <Loading v-if="loading" />
      <h1 v-if="!editPerson">S'Inscrire</h1>
      <h1 v-else>Modification des Informations</h1>
      <div class="bill-from flex flex-column">
        <div class="input flex flex-column">
          <label for="nom">Nom</label>
          <input
            required
            type="text"
            v-on:keyup="createNewNumero"
            id="nom"
            v-model="person.nom"
            autocomplete="off"
          />
        </div>
        <div class="input flex flex-column">
          <label for="prenom">Prenom</label>
          <input
            required
            type="text"
            id="prenom"
            v-model="person.prenom"
            autocomplete="off"
          />
        </div>
        <div class="input flex flex-column">
          <label for="email">Email</label>
          <input
            required
            type="email"
            id="email"
            v-model="person.email"
            autocomplete="off"
          />
        </div>
        <div class="input flex flex-column">
          <label for="univ">Univ/Ecole</label>
          <input
            required
            v-on:keyup="createNewNumero"
            type="text"
            id="univ"
            v-model="person.univ"
            autocomplete="off"
          />
        </div>
        <div class="input flex flex-column">
          <label for="cin">CIN</label>
          <input
            v-on:keyup="createNewNumero"
            required
            type="text"
            id="cin"
            v-model="person.CIN"
            autocomplete="off"
            maxlength="12"
            minlength="12"
          />
        </div>
        <div class="input flex flex-column">
          <label for="telephone">Téléphone</label>
          <input
            type="text"
            v-on:keyup="createNewNumero"
            id="telephone"
            v-model="person.telephone"
            autocomplete="off"
            maxlength="13"
            minlength="10"
          />
        </div>
        <div class="input flex flex-column">
          <label for="dateInscription">Date d'Inscription</label>
          <input
            type="text"
            id="dateInscription"
            v-model="person.date_d_inscription"
            :readonly="true"
          />
        </div>
        <div class="input flex flex-column">
          <label for="sexe">Sexe</label>
          <select v-model="person.sexe" required id="sexe">
            <option value="H">Homme</option>
            <option value="F">Femme</option>
          </select>
        </div>
        <div class="input flex flex-column">
          <label for="num">Identifiant</label>
          <input
            disabled
            required
            type="text"
            id="num"
            v-model="person.numero"
          />
        </div>
      </div>

      <div class="save flex">
        <div class="left">
          <button type="button" @click="closePerson" class="red">
            Annuler
          </button>
        </div>
        <div class="right flex">
          <button
            v-if="!editPerson"
            type="submit"
            @click="publishPerson"
            class="purple"
          >
            Créer
          </button>
          <button v-else type="submit" @click="publishPerson" class="purple">
            Modififer
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapMutations, mapState } from "vuex";
import Loading from "../components/Loading.vue";
export default {
  name: "personModal",
  components: { Loading },
  data() {
    return {
      dateOption: { year: "numeric", month: "short", day: "numeric" },
      person: {
        nom: null,
        prenom: null,
        numero: null,
        email: null,
        univ: null,
        CIN: null,
        sexe: null,
        date_d_inscription: null,
        telephone: null,
      },
      personPending: null,
      personDraft: null,
      personItemList: [],
      loading: null,
    };
  },
  created() {
    if (!this.editPerson) {
      //get current date
      const now = Date.now();
      this.person.date_d_inscription = new Date(now).toLocaleDateString(
        "fr-FR",
        this.dateOption
      );
    } else {
      const currentPerson = this.currentPersonArray[0];
      currentPerson.date_d_inscription = new Date(
        currentPerson.date_d_inscription
      ).toLocaleDateString("fr-FR", this.dateOption);
      this.person = {
        id: currentPerson.id,
        numero: currentPerson.numero,
        nom: currentPerson.nom,
        prenom: currentPerson.prenom,
        email: currentPerson.email,
        univ: currentPerson.univ,
        telephone: currentPerson.telephone,
        CIN: currentPerson.CIN,
        date_d_inscription: currentPerson.date_d_inscription,
        sexe: currentPerson.sexe,
      };
    }
  },
  methods: {
    ...mapMutations([
      "TOGGLE_PERSON",
      "TOGGLE_NOTIF_MODAL",
      "TOGGLE_MODAL",
      "TOGGLE_UPDATE_PERSON",
    ]),
    ...mapActions(["UPDATE_PERSON", "GET_ALL_PERSONS"]),
    closePerson() {
      this.TOGGLE_PERSON();
      if (this.editPerson) {
        this.TOGGLE_UPDATE_PERSON();
      }
    },
    publishPerson() {
      this.personPending = true;
    },

    async uploadPerson() {
      const showData = this.personData.filter((person) => {
        return (
          person.CIN == this.person.CIN ||
          person.telephone == this.person.telephone ||
          person.email == this.person.email
        );
        // || person.nom == this.person.nom
      });
      if (showData.length === 0) {
        this.loading = true;
        await axios
          .post(
            `${process.env.VUE_APP_BASE_URL}?action=insert`,
            JSON.stringify(this.person),
            {
              headers: {
                "Content-Type": "text/plain",
              },
            }
          )
          .then((res) => {
            if (res.data.success) {
              this.loading = false;
              this.TOGGLE_PERSON();
              this.GET_ALL_PERSONS();
            }
          })
          .catch((err) => console.log(err));
      } else {
        this.TOGGLE_NOTIF_MODAL();
      }
    },

    async updatePerson() {
      this.loading = true;
      await axios
        .post(
          `${process.env.VUE_APP_BASE_URL}?action=update&id=${this.$route.params.idPerson}`,
          JSON.stringify(this.person),
          {
            headers: {
              "Content-Type": "text/plain",
            },
          }
        )
        .then((res) => {
          if (res.data.success) {
            this.loading = false;
            this.GET_ALL_PERSONS();
            const data = {
              id: this.person.id,
              routeId: this.$route.params.idPerson,
            };
            this.UPDATE_PERSON(data);
          }
        })
        .catch((err) => console.log(err));
    },
    submitForm() {
      if (this.editPerson) {
        this.updatePerson();
        return;
      }
      this.uploadPerson();
    },
    checkClick(e) {
      if (e.target == this.$refs.personWrap) {
        this.TOGGLE_MODAL();
      }
    },

    createNewNumero() {
      if (
        this.person.CIN != null &&
        this.person.telephone != null &&
        this.person.nom != null &&
        this.person.prenom != null &&
        this.person.univ != null
      ) {
        const nom = this.person.nom.substring(0, 1);
        const prenom = this.person.prenom.substring(0, 1);
        const CIN = this.person.CIN.toString().slice(-3);
        const univ = this.person.univ.substring(0, 3);
        const telephone = this.person.telephone.toString().slice(-2);
        const id = this.makeRandom(3);
        this.person.numero = nom + prenom + telephone + univ + CIN + id;
      }
    },

    makeRandom(length) {
      var result = "";
      var characters =
        "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      var charactersLength = characters.length;
      for (var i = 0; i < length; i++) {
        result += characters.charAt(
          Math.floor(Math.random() * charactersLength)
        );
      }
      return result;
    },
  },

  computed: {
    ...mapState(["editPerson", "currentPersonArray", "personData"]),
  },
};
</script>

<style lang="scss" scoped>
.person-wrap {
  position: fixed;
  top: 0;
  left: 0;
  background-color: transparent;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
  @media (min-width: 900px) {
    left: 90px;
  }

  .person-content {
    position: relative;
    padding: 56px;
    max-width: 700px;
    widows: 100%;
    background-color: #141625;
    color: #f1f1f1;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
    h1 {
      margin-bottom: 48px;
      color: #f1f1f1;
    }
    .save {
      margin-top: 60px;
      div {
        flex: 1;
      }
      .right {
        justify-content: flex-end;
      }
    }
  }
  .input {
    margin-bottom: 24px;
  }

  label {
    font-size: 12px;
    margin-bottom: 6px;
  }

  input,
  select {
    width: 100%;
    background-color: #1e2139;
    color: #f1f1f1;
    border-radius: 4px;
    padding: 12px;
    border: none;
    box-sizing: border-box;
    &:focus {
      outline: none;
      border: 2px solid #7c5dfa;
    }
  }
}
</style>
