<template>
  <div class="person-view container">
    <a class="nav-link flex" @click="goBack">
      <img src="@/assets/icon-arrow-left.svg" alt="" /> Return
    </a>
    <div class="header flex">
      <div class="left flex">
        <h3>Information</h3>
        <button
          class="note"
          @click="TOGGLE_NOTE_MODAL"
          v-if="currentPerson.validation === 'OUI'"
        >
          Noté <span></span>
        </button>
      </div>
      <div class="center">
        <button
          class="valide"
          @click="validatePerson('NON')"
          v-if="currentPerson.validation === 'OUI'"
        >
          Validé
        </button>
        <button
          class="non-valide"
          @click="validatePerson('OUI')"
          v-if="currentPerson.validation === 'NON'"
        >
          Non Validé
        </button>
      </div>
      <div class="right flex">
        <button class="dark-purple" @click="toggleEditPerson">Edit</button>
        <button class="red" @click="TOGGLE_DELETE_MODAL">Delete</button>
      </div>
    </div>
    <div class="person-details flex flex-column">
      <div class="desc">
        <div class="desc-left flex" :class="currentPerson.status">
          {{ currentPerson.status }}
        </div>
      </div>
      <div class="data flex">
        <div class="left flex">ID</div>
        <div class="right flex">{{ currentPerson.numero }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">Numéro</div>
        <div class="right flex">#{{ currentPerson.id }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">Nom et Prénom</div>
        <div class="right flex">
          {{ currentPerson.nom }}
          {{ currentPerson.prenom }}
        </div>
      </div>
      <div class="data flex">
        <div class="left flex">Email</div>
        <div class="right flex">{{ currentPerson.email }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">Univ/Ecole</div>
        <div class="right flex">{{ currentPerson.univ }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">Numéro téléphone</div>
        <div class="right flex">{{ currentPerson.telephone }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">CIN</div>
        <div class="right flex">{{ currentPerson.CIN }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">Sexe</div>
        <div class="right flex">{{ currentPerson.sexe }}</div>
      </div>
      <div class="data flex">
        <div class="left flex">Date d'Inscription</div>
        <div class="right flex">{{ dateInscri }}</div>
      </div>
      <div class="desc" v-if="currentPerson.description">
        <div class="desc-left flex">Déscripton</div>
        <div class="desc-right flex">
          {{ currentPerson.description }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
import axios from "axios";
export default {
  name: "detailPerson",
  data() {
    return {
      currentPerson: null,
      dateInscri: null,
      dateOption: { year: "numeric", month: "short", day: "numeric" },
    };
  },
  created() {
    this.getCurrentPerson();
  },
  methods: {
    ...mapMutations([
      "SET_CURRENT_PERSON",
      "TOGGLE_UPDATE_PERSON",
      "TOGGLE_PERSON",
      "TOGGLE_NOTE_MODAL",
      "TOGGLE_DELETE_MODAL",
    ]),
    ...mapActions(["VALIDATE_PERSON", "GET_ALL_PERSONS", "DELETE_THE_PERSON"]),
    goBack() {
      this.$router.go(-1);
    },
    getCurrentPerson() {
      this.SET_CURRENT_PERSON(this.$route.params.idPerson);
      this.currentPerson = this.currentPersonArray[0];
      this.dateInscri = new Date(
        this.currentPerson.date_d_inscription
      ).toLocaleDateString("fr-FR", this.dateOption);
    },
    toggleEditPerson() {
      this.TOGGLE_UPDATE_PERSON();
      this.TOGGLE_PERSON();
    },

    async validatePerson(val) {
      await axios
        .post(
          `${process.env.VUE_APP_BASE_URL}?action=validation&id=${this.$route.params.idPerson}`,
          JSON.stringify({ valide: val }),
          {
            headers: {
              "Content-Type": "text/plain",
            },
          }
        )
        .then((res) => {
          if (res.data.success) {
            this.GET_ALL_PERSONS();
            const data = {
              id: this.currentPerson.id,
              routeId: this.$route.params.idPerson,
            };
            this.VALIDATE_PERSON(data);
          }
        })
        .catch((err) => console.log(err));
    },
  },
  computed: {
    ...mapState([
      "currentPersonArray",
      "editPerson",
      "modalNote",
      "validatedPerson",
    ]),
  },
  watch: {
    editPerson() {
      if (!this.editPerson) {
        this.currentPerson = this.currentPersonArray[0]; //ito n solonle eo
      }
    },
    modalNote() {
      if (!this.editPerson) {
        this.currentPerson = this.currentPersonArray[0]; //ito n solonle eo
      }
    },
    validatedPerson() {
      if (this.validatedPerson != null)
        this.currentPerson = this.currentPersonArray[0];
    },
  },
};
</script>

<style lang="scss" scoped>
.person-view {
  .nav-link {
    margin-bottom: 32px;
    align-items: center;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    &:hover {
      color: #09e051;
    }
    img {
      margin-left: 16px;
      width: 20px;
      height: 20px;
      margin-right: 10px;
    }
  }
  .header {
    align-items: center;
    padding: 24px 32px;
    font-size: 12px;
    background-color: #1e2129 !important;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    .left {
      align-items: center;
      background-color: #1e2129;
      .note {
        background: #094de0;
      }
      h3 {
        color: #dfe3fa;
        margin-right: 16px;
        font-size: 18px;
        background-color: #1e2129;
      }
    }

    .center {
      flex: 1;
      text-align: end;
      background: transparent;

      .valide {
        background: #09e051;
      }
      .non-valide {
        background: #ff9100;
      }
    }
    .right {
      flex: 1;
      justify-content: flex-end;
      background-color: #1e2129;
    }
  }

  .person-details {
    .loading {
      @keyframes spin {
        to {
          transform: rotateZ(360deg);
        }
      }
      span {
        display: block;
        width: 60px;
        height: 60px;
        margin: 0 auto;
        border: 3px solid transparent;
        border-top-color: #fff;
        border-bottom-color: #fff;
        border-radius: 50%;
        animation: spin ease 1000ms infinite;
      }
    }

    width: 100%;
    background-color: #1e2129 !important;
    padding: 24px 32px;
    div {
      background-color: #1e2129 !important;
    }

    .desc {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .desc-left {
        font-weight: bold;
        color: #7c5dfa;

        &.OK {
          color: #09e051;
        }
        &.KO {
          color: rgb(255, 0, 0);
        }
        &.WAITING {
          color: yellow;
        }
      }
      .desc-right {
        color: #fff;
      }
    }
    .data {
      padding: 15px 0;
      font-size: 14px;

      .left {
        align-items: center;
        font-weight: bold;
        color: #7c5dfa;
      }
      .right {
        flex: 1;
        justify-content: flex-end;
        color: #fff;
      }
    }
  }
}
</style>
