<template>
  <div class="home container">
    <!-- Header  -->
    <div class="header flex">
      <div class="left flex flex-column">
        <h1>Les Personnes payées</h1>
        <span
          >Total des personnes payées : <b>{{ filteredData.length }}</b></span
        >
      </div>
      <div class="right flex">
        <div class="filter flex" @click="toggleFilterMenu">
          <span
            >Filter par
            <span v-if="filterPerson">: {{ filterPerson }}</span></span
          >
          <img src="@/assets/icon-arrow-down.svg" alt="" />
          <ul v-show="filterMenu" class="filter-menu">
            <li @click="filterPersons">Homme</li>
            <li @click="filterPersons">Femme</li>
            <li @click="filterPersons">Tous</li>
          </ul>
        </div>
      </div>
    </div>

    <!-- List Person  -->
    <div v-if="filteredData.length > 0">
      <div class="search">
        <input
          type="text"
          id="numero"
          ref="Numero"
          autocomplete="off"
          v-model="searchVal"
          placeholder="n° Identification ou email ou CIN ou telephone seulement"
          @keyup="search()"
        />
        <!-- <button class="purple" @click="toggleModalSearch">Rechercher</button> -->
      </div>
      <ListPerson
        v-for="(persons, index) in filteredData"
        :key="index"
        :persons="persons"
      />
    </div>
    <div v-else class="empty flex flex-column">
      <img src="@/assets/illustration-empty.svg" alt="" />
      <h3>There is nothing here</h3>
      <p>Add a new Person by clicking the new Person button and get started</p>
      <button class="bg-linear-blue" @click="getAll">Refresh</button>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import ListPerson from "../components/ListPerson.vue";
export default {
  name: "validation",
  components: { ListPerson },
  data() {
    return {
      filterMenu: null,
      filterPerson: null,
      filteredData: [],
      searchVal: "",
    };
  },
  created() {
    this.getAll();
  },
  methods: {
    ...mapMutations(["TOGGLE_PERSON", "TOGGLE_SEARCH_MODAL"]),
    newPerson() {
      this.TOGGLE_PERSON();
    },
    toggleModalSearch() {
      this.TOGGLE_SEARCH_MODAL();
    },
    toggleFilterMenu() {
      this.filterMenu = !this.filterMenu;
    },

    getAll() {
      console.log(this.personData);
      this.filteredData = this.personData.filter((data) => {
        return data.validation === "OUI" && data.status === "OK";
      });
      this.searchVal = "";
    },
    search() {
      const sea = this.personData.filter((data) => {
        const value = this.searchVal;
        if (value == null)
          return data.validation === "OUI" && data.status === "OK";
        if (value === data.numero) {
          return (
            data.numero === value &&
            data.validation === "OUI" &&
            data.status === "OK"
          );
        }
        if (value == data.CIN) {
          return (
            data.CIN == value &&
            data.validation === "OUI" &&
            data.status === "OK"
          );
        }
        if (value === data.email) {
          return (
            data.email === value &&
            data.validation === "OUI" &&
            data.status === "OK"
          );
        }
        if (parseInt(value) === data.id) {
          return (
            data.id === parseInt(value) &&
            data.validation === "OUI" &&
            data.status === "OK"
          );
        }
        if (value.toLowerCase() == data.univ.toLowerCase()) {
          return (
            data.univ.toLowerCase() == value.toLowerCase() &&
            data.validation === "OUI" &&
            data.status === "OK"
          );
        }
        if (value == data.telephone) {
          return (
            data.telephone == value.toLowerCase() &&
            data.validation === "OUI" &&
            data.status === "OK"
          );
        }
      });

      this.filteredData = sea;
    },
    filterPersons(e) {
      if (e.target.innerText == "Tous") {
        this.filterPerson = null;
        this.filteredData = this.personData.filter((data) => {
          return data.validation === "OUI" && data.status === "OK";
        });
      }
      if (e.target.innerText == "Homme") {
        this.filteredData = this.personData.filter((data) => {
          if (e.target.innerText === "Homme") {
            return (
              data.sexe === "H" &&
              data.validation === "OUI" &&
              data.status === "OK"
            );
          }
        });
      }
      if (e.target.innerText == "Femme") {
        this.filteredData = this.personData.filter((data) => {
          if (e.target.innerText === "Femme") {
            return (
              data.sexe === "F" &&
              data.validation === "OUI" &&
              data.status === "OK"
            );
          }
        });
      }
    },
  },
  computed: {
    ...mapState(["personData", "searchValue"]),
  },
};
</script>

<style lang="scss" scoped>
.home {
  color: #f1f1f1;
  .header {
    margin-bottom: 15px;
    h1 {
      text-transform: uppercase;
    }
    .left,
    .right {
      flex: 1;
    }
    .right {
      justify-content: flex-end;
      align-items: center;
      .button,
      .filter {
        align-items: center;
        span {
          font-size: 12px;
        }
      }
    }
    .filter {
      position: relative;
      margin-right: 40px;
      cursor: pointer;
      img {
        margin-left: 12px;
        width: 9px;
        height: 5px;
      }

      .filter-menu {
        width: 120px;
        position: absolute;
        top: 25px;
        list-style: none;
        background-color: #1e2139;
        box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
          0 2px 4px -1px rgba(0, 0, 0, 0.06);
        li {
          cursor: pointer;
          font-size: 12px;
          padding: 10px 20px;
          &:hover {
            background-color: #f1f1f1;
            color: #1e2139;
            font-weight: 700;
          }
        }
      }
    }
    .button {
      padding: 8px 10px;
      background-color: #7c5dfa;
      border-radius: 40px;
      span {
        background: #7c5dfa;
      }
      .inner-button {
        margin-right: 8px;
        border-radius: 50%;
        padding: 8px;
        align-items: center;
        justify-content: center;
        background-color: #f1f1f1;
        img {
          widows: 10px;
          height: 10px;
          background: transparent;
        }
      }
    }
  }

  .empty {
    margin-top: 65px;
    align-items: center;
    button {
      margin-top: 10px;
    }
    img {
      width: 214px;
      height: 200px;
    }
    h3 {
      font-size: 20px;
      margin-top: 40px;
    }
    p {
      text-align: center;
      max-width: 224px;
      font-size: 12px;
      font-weight: 300;
      margin-top: 16px;
    }
  }

  input,
  select {
    width: 50%;
    background-color: #1e2139;
    color: #f1f1f1;
    border-radius: 4px;
    padding: 12px;
    border: none;
    box-sizing: border-box;
    &:focus {
      outline: none;
      border: 2px solid #7c5dfa;
    }
  }
  .search {
    width: 100%;
    margin-bottom: 15px;
    button {
      border-radius: 8px;
      font-size: 13px;
    }
  }
}
</style>
