<template>
  <div class="home container">
    <!-- Header  -->
    <div class="header flex">
      <div class="left flex flex-column">
        <h1>Inscription</h1>
        <span
          >Total des personnes inscrits : <b>{{ filteredData.length }}</b></span
        >
      </div>
      <div class="right flex">
        <div class="filter flex" @click="toggleFilterMenu">
          <span
            >Filter par validation:
            <span v-if="filterPerson">: {{ filterPerson }}</span></span
          >
          <img src="@/assets/icon-arrow-down.svg" alt="" />
          <ul v-show="filterMenu" class="filter-menu">
            <li @click="filterPersons">Validé</li>
            <li @click="filterPersons">Non-validé</li>
            <li @click="filterPersons">Tous</li>
          </ul>
        </div>
        <div @click="newPerson" class="button bg-linear-blue flex">
          <div class="inner-button flex">
            <img src="@/assets/icon-plus.svg" alt="" />
          </div>
          <span >Nouveau</span>
        </div>
      </div>
    </div>

    <!-- List Person  -->
    <div v-if="filteredData.length > 0">
      <div class="search">
        <button class="purple" @click="toggleModalSearch">Rechercher</button>
      </div>
      <ListPerson
        v-for="(persons, index) in filteredData"
        :key="index"
        :persons="persons"
      />
    </div>
    <div v-else class="empty flex flex-column">
      <img src="@/assets/illustration-empty.svg" alt="" />
      <h3>There is nothing here</h3>
      <p>Add a new Person by clicking the new Person button and get started</p>
      <button class="bg-linear-blue" @click="getAll">Refresh</button>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import ListPerson from "../components/ListPerson.vue";
export default {
  name: "Inscription",
  components: { ListPerson },
  data() {
    return {
      filterMenu: null,
      filterPerson: null,
      filteredData: [],
    };
  },
  methods: {
    ...mapMutations(["TOGGLE_PERSON", "TOGGLE_SEARCH_MODAL"]),
    getAll() {
      this.filteredData = this.personData;
    },
    newPerson() {
      this.TOGGLE_PERSON();
    },
    toggleModalSearch() {
      this.TOGGLE_SEARCH_MODAL();
    },
    toggleFilterMenu() {
      this.filterMenu = !this.filterMenu;
    },
    filterPersons(e) {
      this.filterPerson =
        e.target.innerText === "Tous" ? "" : e.target.innerText;
      if (e.target.innerText == "Tous") {
        this.filterPerson = null;
        this.filteredData = this.personData;
      }
      if (e.target.innerText == "Validé") {
        this.filteredData = this.personData.filter((data) => {
          if (e.target.innerText === "Validé") {
            return data.validation === "OUI";
          }
        });
      }
      if (e.target.innerText == "Non-validé") {
        this.filteredData = this.personData.filter((data) => {
          if (e.target.innerText === "Non-validé") {
            return data.validation === "NON";
          }
        });
      }
    },
  },
  watch: {
    searchValue() {
      const sea = this.personData.filter((data) => {
        const value = this.searchValue.numero;
        console.log("value: ", value);
        if (value == null) return;
        if (value === data.numero) {
          return data.numero === value;
        }
        if (value == data.CIN) {
          return data.CIN == value;
        }
        if (value === data.email) {
          return data.email === value;
        }
        if (parseInt(value) === data.id) {
          return data.id === parseInt(value);
        }
        if (value.toLowerCase() == data.univ.toLowerCase()) {
          return data.univ.toLowerCase() == value.toLowerCase();
        }
        if (value == data.telephone) {
          return data.telephone == value.toLowerCase();
        }
      });

      this.filteredData = sea;
    },
  },
  computed: {
    ...mapState(["personData", "searchValue"]),
  },
  created() {
    this.filteredData = this.personData;
  },
};
</script>

<style lang="scss" scoped>
.home {
  color: #f1f1f1;
  .header {
    margin-bottom: 15px;
    .left,
    .right {
      flex: 1;
    }
    .right {
      justify-content: flex-end;
      align-items: center;
      .button,
      .filter {
        align-items: center;
        span {
          font-size: 12px;
        }
      }
    }
    .filter {
      position: relative;
      margin-right: 40px;
      cursor: pointer;
      img {
        margin-left: 12px;
        width: 9px;
        height: 5px;
      }

      .filter-menu {
        width: 120px;
        position: absolute;
        top: 25px;
        list-style: none;
        background-color: #1e2139;
        box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
          0 2px 4px -1px rgba(0, 0, 0, 0.06);
        li {
          cursor: pointer;
          font-size: 12px;
          padding: 10px 20px;
          &:hover {
            background-color: #f1f1f1;
            color: #1e2139;
            font-weight: 700;
          }
        }
      }
    }
    .button {
      padding: 8px 10px;
      background-color: #7c5dfa;
      border-radius: 40px;
      span {
        background: transparent;
      }
      .inner-button {
        margin-right: 8px;
        border-radius: 50%;
        padding: 8px;
        align-items: center;
        justify-content: center;
        background-color: #f1f1f1;
        img {
          widows: 10px;
          height: 10px;
          background: transparent;
        }
      }
    }
  }

  .empty {
    margin-top: 65px;
    align-items: center;
    button {
      margin-top: 10px;
    }
    img {
      width: 214px;
      height: 200px;
    }
    h3 {
      font-size: 20px;
      margin-top: 40px;
    }
    p {
      text-align: center;
      max-width: 224px;
      font-size: 12px;
      font-weight: 300;
      margin-top: 16px;
    }
  }

  .search {
    width: 100%;
    margin-bottom: 15px;
    button {
      border-radius: 8px;
      font-size: 13px;
    }
  }
}
</style>
