<template>
  <div class="modal flex">
    <div class="modal-content red">
      <p>Etes vous sur de le supprimer?</p>
      <div class="actions flex">
        <button @click="TOGGLE_DELETE_MODAL" class="red">NON</button>
        <button @click="deletePerson" class="purple">OK</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from "vuex";

export default {
  name: "Notification",
  methods: {
    ...mapMutations(["TOGGLE_DELETE_MODAL"]),
    ...mapActions(["DELETE_THE_PERSON"]),
    closeModal() {
      this.TOGGLE_NOTIF_MODAL();
    },

    async deletePerson() {
      await this.DELETE_THE_PERSON(this.currentPersonArray[0].id);
      this.$router.push({ name: "Inscription" });
    },
  },
  computed: {
    ...mapState(["currentPersonArray", "modalDelete"]),
  },
  watch: {
    modalDelete() {
      console.log(this.currentPersonArray[0].id);
    },
  },
};
</script>

<style lang="scss" scoped>
.modal {
  z-index: 100;
  position: fixed;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background: transparent;
  .modal-content {
    border-radius: 20px;
    padding: 48px 32px;
    max-width: 450px;
    background-color: #df1d1d;
    color: #fff;
    p {
      text-align: center;
      background: transparent;
    }
    .actions {
      margin-top: 24px;
      background: transparent;

      button {
        flex: 1;
        &:hover {
          background: white;
          color: #df1d1d;
        }
      }
    }
  }
}
</style>
