<template>
  <div class="modal flex">
    <div class="modal-content">
      <div class="header">
        Recherche <span v-if="person.numero"> : "{{ person.numero }}"</span>
      </div>
      <form @submit.prevent="submitForm">
        <div class="content">
          <div class="input flex flex-column">
            <label for="numero"
              >Numéro / N° Identification / Email / CIN / Numéro
              téléphone</label
            >
            <input
              type="text"
              id="numero"
              ref="Numero"
              v-model="person.numero"
              autocomplete="off"
              placeholder="n° Identification ou email ou CIN ou telephone seulement"
            />
          </div>
        </div>
        <div class="footer flex">
          <button @click="closeModalSearch" type="button" class="red">
            Annuler
          </button>
          <button type="submit" class="purple">Voir</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from "vuex";

export default {
  name: "Search",
  data() {
    return {
      person: {
        numero: null,
      },
    };
  },
  created() {
    this.person.numero = null;
  },
  mounted() {
    this.focusInputOnOpen();
  },
  methods: {
    ...mapMutations(["TOGGLE_SEARCH_MODAL"]),
    ...mapActions(["SEARCH_PERSON"]),
    closeModalSearch() {
      this.TOGGLE_SEARCH_MODAL();
    },

    focusInputOnOpen() {
      this.$refs.Numero.focus();
    },
    search() {
      const data = {
        numero: this.person.numero,
      };
      this.SEARCH_PERSON(data);
      this.TOGGLE_SEARCH_MODAL();
    },
    submitForm() {
      if (this.person.numero) {
        this.$router.push({ name: "Inscription" });
        this.search();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.modal {
  z-index: 100;
  position: fixed;
  justify-content: center;
  //   align-items: center;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  .modal-content {
    z-index: 101;
    border-radius: 20px;
    padding: 48px 32px;
    margin-top: 50px;
    width: 700px;
    max-width: 700px;
    background-color: #212333;
    color: #fff;
    position: absolute;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
    p {
      text-align: center;
      background: transparent;
    }

    div,
    p,
    label,
    form,
    span {
      background: transparent;
    }

    .close {
      position: absolute;
      top: 10px;
      right: 10px;
    }

    .header {
      padding: 10px 0;
      font-size: 20px;
    }

    .content {
      .input {
        margin-bottom: 24px;
      }

      label {
        font-size: 12px;
        margin-bottom: 6px;
        background: transparent;
      }

      input,
      select {
        width: 100%;
        background-color: #1e2139;
        color: #f1f1f1;
        border-radius: 4px;
        padding: 12px;
        border: none;
        box-sizing: border-box;
        &:focus {
          outline: none;
          border: 2px solid #7c5dfa;
        }
      }
    }
  }
}
</style>
