<template>
  <div class="modal flex">
    <div class="modal-content">
      <form @submit.prevent="submitForm" class="person-content">
        <div class="bill-from flex flex-column">
          <div class="input flex flex-column">
            <label for="status">Status</label>
            <select required id="status" v-model="note.status">
              <option value="KO">KO</option>
              <option value="WAITING">WAITING</option>
              <option value="OK">OK</option>
            </select>
          </div>
          <div class="input flex flex-column">
            <label for="desc">Description</label>
            <textarea
              v-model="note.description"
              type="text"
              id="desc"
              autocomplete="off"
            ></textarea>
          </div>
        </div>

        <div class="save flex">
          <div class="left">
            <button type="button" @click="TOGGLE_NOTE_MODAL" class="red">
              Annuler
            </button>
          </div>
          <div class="right flex">
            <button type="submit" class="purple">Sauvegarder</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapActions, mapState } from "vuex";
import axios from "axios";
export default {
  name: "note",
  data() {
    return {
      note: { status: null, description: null },
    };
  },

  created() {
    this.note = {
      status: this.currentPersonArray[0]?.status,
      description: this.currentPersonArray[0]?.description,
    };
  },
  methods: {
    ...mapMutations(["TOGGLE_NOTE_MODAL"]),
    ...mapActions(["NOTE_PERSON", "GET_ALL_PERSONS"]),
    async submitForm() {
      await axios
        .post(
          `${process.env.VUE_APP_BASE_URL}?action=status&id=${this.$route.params.idPerson}`,
          this.note,
          {
            headers: {
              "Content-Type": "text/plain",
            },
          }
        )
        .then((res) => {
          if (res.data.success) {
            this.GET_ALL_PERSONS();
            const data = {
              id: this.currentPersonArray[0].id,
              routeId: this.$route.params.idPerson,
            };
            this.NOTE_PERSON(data);
          }
        })
        .catch((err) => console.log(err));
    },
  },
  computed: {
    ...mapState(["currentPersonArray", "modalNote"]),
  },

  watch: {
    modalNote() {
      this.note = {
        status: this.currentPersonArray[0].status,
        description: this.currentPersonArray[0].description,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.modal {
  z-index: 100;
  position: fixed;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background: transparent;
  .modal-content {
    border-radius: 20px;
    padding: 48px 32px;
    width: 500px;
    color: #fff;
    background: #1e2139;
    form {
      background: #1e2139;
    }
    .save {
      margin-top: 60px;
      div {
        flex: 1;
        background: #1e2139;
      }
      .right {
        justify-content: flex-end;
      }
    }
    label {
      font-size: 12px;
      padding-bottom: 10px;
      padding-top: 10px;
      background: #1e2139;
    }
    textarea,
    select {
      width: 100%;
      background-color: #171925;
      color: #f1f1f1;
      border-radius: 4px;
      padding: 12px 15px 12px 12px;
      border: none;
      box-sizing: border-box;
      &:focus {
        outline: none;
        border: 2px solid #7c5dfa;
      }
    }
    .actions {
      margin-top: 24px;
      background: transparent;

      button {
        flex: 1;
        &:hover {
          background: white;
          color: #df1d1d;
        }
      }
    }
  }
}
</style>
